"use strict";
const queryable_1 = require("./queryable");
/**
 * Describes a collection of Field objects
 *
 */
class Forms extends queryable_1.QueryableCollection {
    /**
     * Creates a new instance of the Fields class
     *
     * @param baseUrl The url or Queryable which forms the parent of this fields collection
     */
    constructor(baseUrl, path = "forms") {
        super(baseUrl, path);
    }
    /**
     * Gets a form by id
     *
     * @param id The guid id of the item to retrieve
     */
    getById(id) {
        let i = new Form(this);
        i.concat(`('${id}')`);
        return i;
    }
}
exports.Forms = Forms;
/**
 * Describes a single of Form instance
 *
 */
class Form extends queryable_1.QueryableInstance {
    /**
     * Creates a new instance of the Form class
     *
     * @param baseUrl The url or Queryable which is the parent of this form instance
     */
    constructor(baseUrl, path) {
        super(baseUrl, path);
    }
}
exports.Form = Form;



