"use strict";
const queryable_1 = require("./queryable");
const webs_1 = require("./webs");
const usercustomactions_1 = require("./usercustomactions");
const odata_1 = require("./odata");
const features_1 = require("./features");
/**
 * Describes a site collection
 *
 */
class Site extends queryable_1.QueryableInstance {
    /**
     * Creates a new instance of the RoleAssignments class
     *
     * @param baseUrl The url or Queryable which forms the parent of this fields collection
     */
    constructor(baseUrl, path = "_api/site") {
        super(baseUrl, path);
    }
    /**
     * Gets the root web of the site collection
     *
     */
    get rootWeb() {
        return new webs_1.Web(this, "rootweb");
    }
    /**
     * Gets the active features for this site
     *
     */
    get features() {
        return new features_1.Features(this);
    }
    /**
     * Get all custom actions on a site collection
     *
     */
    get userCustomActions() {
        return new usercustomactions_1.UserCustomActions(this);
    }
    /**
     * Gets the context information for the site.
     */
    getContextInfo() {
        let q = new Site(this.parentUrl, "_api/contextinfo");
        return q.post().then(data => {
            if (data.hasOwnProperty("GetContextWebInformation")) {
                let info = data.GetContextWebInformation;
                info.SupportedSchemaVersions = info.SupportedSchemaVersions.results;
                return info;
            }
            else {
                return data;
            }
        });
    }
    /**
     * Gets the document libraries on a site. Static method. (SharePoint Online only)
     *
     * @param absoluteWebUrl The absolute url of the web whose document libraries should be returned
     */
    getDocumentLibraries(absoluteWebUrl) {
        let q = new queryable_1.Queryable("", "_api/sp.web.getdocumentlibraries(@v)");
        q.query.add("@v", "'" + absoluteWebUrl + "'");
        return q.get().then(data => {
            if (data.hasOwnProperty("GetDocumentLibraries")) {
                return data.GetDocumentLibraries;
            }
            else {
                return data;
            }
        });
    }
    /**
     * Gets the site URL from a page URL.
     *
     * @param absolutePageUrl The absolute url of the page
     */
    getWebUrlFromPageUrl(absolutePageUrl) {
        let q = new queryable_1.Queryable("", "_api/sp.web.getweburlfrompageurl(@v)");
        q.query.add("@v", "'" + absolutePageUrl + "'");
        return q.get().then(data => {
            if (data.hasOwnProperty("GetWebUrlFromPageUrl")) {
                return data.GetWebUrlFromPageUrl;
            }
            else {
                return data;
            }
        });
    }
    /**
     * Creates a new batch for requests within the context of context this site
     *
     */
    createBatch() {
        return new odata_1.ODataBatch(this.parentUrl);
    }
}
exports.Site = Site;



