"use strict";
const queryable_1 = require("./queryable");
/**
 * Describes a collection of List objects
 *
 */
class Features extends queryable_1.QueryableCollection {
    /**
     * Creates a new instance of the Lists class
     *
     * @param baseUrl The url or Queryable which forms the parent of this fields collection
     */
    constructor(baseUrl, path = "features") {
        super(baseUrl, path);
    }
    /**
     * Gets a list from the collection by guid id
     *
     * @param id The Id of the feature (GUID)
     */
    getById(id) {
        let feature = new Feature(this);
        feature.concat(`('${id}')`);
        return feature;
    }
    /**
     * Adds a new list to the collection
     *
     * @param id The Id of the feature (GUID)
     * @param force If true the feature activation will be forced
     */
    add(id, force = false) {
        let adder = new Features(this, "add");
        return adder.post({
            body: JSON.stringify({
                featdefScope: 0,
                featureId: id,
                force: force,
            }),
        }).then(data => {
            return {
                data: data,
                feature: this.getById(id),
            };
        });
    }
    /**
     * Removes (deactivates) a feature from the collection
     *
     * @param id The Id of the feature (GUID)
     * @param force If true the feature deactivation will be forced
     */
    remove(id, force = false) {
        let remover = new Features(this, "remove");
        return remover.post({
            body: JSON.stringify({
                featureId: id,
                force: force,
            }),
        });
    }
}
exports.Features = Features;
class Feature extends queryable_1.QueryableInstance {
    /**
     * Creates a new instance of the Lists class
     *
     * @param baseUrl The url or Queryable which forms the parent of this fields collection
     */
    constructor(baseUrl, path) {
        super(baseUrl, path);
    }
    /**
     * Removes (deactivates) a feature from the collection
     *
     * @param force If true the feature deactivation will be forced
     */
    deactivate(force = false) {
        let removeDependency = this.addBatchDependency();
        let idGet = new Feature(this).select("DefinitionId");
        return idGet.getAs().then(feature => {
            let promise = this.getParent(Features, this.parentUrl, "").remove(feature.DefinitionId, force);
            removeDependency();
            return promise;
        });
    }
}
exports.Feature = Feature;



